"use client";

// Tokens used: bg-bg, bg-surface, border-border, text-text, text-text-muted, brand, accent
// Component: Lecturer Upload Results Page (Light Theme)

import { useState } from "react";
import theme from "@/styles/theme";

interface UploadRow {
  studentName: string;
  matricNumber: string;
  score: number | "";
}

export default function UploadResultsPage() {
  const [courseCode, setCourseCode] = useState("");
  const [rows, setRows] = useState<UploadRow[]>([
    { studentName: "Adeyemi Adebayo", matricNumber: "2021/12345", score: "" },
    { studentName: "Muna James", matricNumber: "2021/12222", score: "" },
  ]);
  const [loading, setLoading] = useState(false);

  const handleScoreChange = (index: number, score: string) => {
    const updated = [...rows];
    updated[index].score = score ? Number(score) : "";
    setRows(updated);
  };

  const handleSubmit = async () => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      alert("Results uploaded successfully!");
    }, 1200);
  };

  return (
    <div className="max-w-6xl mx-auto p-6 min-h-screen bg-bg text-text">
      {/* Page Title */}
      <h1 className="text-2xl font-bold mb-4">Upload Course Results</h1>
      <p className="text-text-muted mb-6">
        Enter student scores for your selected course.
      </p>

      {/* Course Selection */}
      <div className="bg-surface border border-border rounded-xl p-4 mb-6 shadow-md">
        <label
          htmlFor="courseCode"
          className="block font-medium mb-2 text-text"
        >
          Course Code
        </label>
        <input
          id="courseCode"
          type="text"
          value={courseCode}
          onChange={(e) => setCourseCode(e.target.value)}
          placeholder="e.g., CSC 401"
          className="w-full border border-border rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-brand"
        />
      </div>

      {/* Results Table */}
      <div className="bg-surface border border-border rounded-xl shadow-md overflow-x-auto">
        <table className="w-full text-sm">
          <thead className="bg-surface-elevated border-b border-border text-text-muted">
            <tr>
              <th className="text-left p-3">#</th>
              <th className="text-left p-3">Student Name</th>
              <th className="text-left p-3">Matric No</th>
              <th className="text-left p-3">Score</th>
            </tr>
          </thead>
          <tbody>
            {rows.map((row, index) => (
              <tr key={index} className="border-b border-border hover:bg-bg/60">
                <td className="p-3">{index + 1}</td>
                <td className="p-3">{row.studentName}</td>
                <td className="p-3">{row.matricNumber}</td>
                <td className="p-3">
                  <input
                    type="number"
                    value={row.score}
                    min="0"
                    max="100"
                    onChange={(e) => handleScoreChange(index, e.target.value)}
                    className="w-24 border border-border rounded-md px-2 py-1 focus:ring-2 focus:ring-brand"
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Submit Button */}
      <div className="mt-6 flex justify-end">
        <button
          onClick={handleSubmit}
          disabled={loading}
          className="bg-brand text-white font-semibold px-6 py-2 rounded-lg hover:bg-brand/90 transition-colors disabled:opacity-60"
          style={{ backgroundColor: theme.colors.primary }}
        >
          {loading ? "Uploading..." : "Submit Results"}
        </button>
      </div>
    </div>
  );
}
